@charset "UTF-8";
/**
Andy Bell
https://hankchizljaw.com/wrote/a-modern-css-reset/
 */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul[class],
ol[class] {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul[class],
ol[class] {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.hide {
  display: none !important;
}
@media (min-width: 1280px) {
  .hide-desktop {
    display: none !important;
  }
}
@media (min-width: 768px) and (max-width: 1279px) {
  .hide-tablet {
    display: none !important;
  }
}
@media (max-width: 767px) {
  .hide-mobile {
    display: none !important;
  }
}

html,
body {
  height: 100%;
}

body {
  min-height: 100vh;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #444;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

a {
  color: #068;
  transition: color 0.2s linear;
}
a:hover {
  color: #00b3ee;
}

.wrap {
  background-color: #fff;
  padding: 0 calc((100% - 300px) / 2);
}
@media (min-width: 768px) {
  .wrap {
    padding: 0 calc((100% - 720px) / 2);
  }
}
@media (min-width: 1280px) {
  .wrap {
    padding: 0 calc((100% - 1200px) / 2);
  }
}

.header {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: inset 0 -1px 0 #ccc;
  position: sticky;
  top: 0;
  width: 100%;
}
@media (max-width: 767px) {
  .header {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.header-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .header-logo {
    margin-right: 40px;
    width: 200px;
  }
}
@media (min-width: 1280px) {
  .header-logo {
    width: 300px;
  }
}
.header-logo svg {
  display: block;
  margin: 0 auto;
  width: 150px;
  height: 120px;
  fill: #e80;
  transition: ease all 0.8s;
}
@media (max-width: 767px) {
  .header-logo svg {
    width: 50px;
    height: 40px;
  }
}
.header-nav {
  align-self: center;
  flex-grow: 1;
}
@media (max-width: 767px) {
  .header-nav {
    margin-top: 20px;
    width: 100%;
  }
}
.header-menu {
  display: flex;
}
.header-menu-item {
  flex: 1 0 0;
  text-align: center;
}
.header-menu-item:not(:first-child) {
  box-shadow: inset 1px 0 0 #ccc;
}

.main {
  display: flex;
  flex: 1 1 auto;
}
@media (min-width: 768px) {
  .main-sidebar {
    flex-shrink: 0;
    margin-right: 40px;
    padding: 20px;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
  }
}
@media (max-width: 1279px) {
  .main-sidebar {
    width: 200px;
  }
}
@media (min-width: 1280px) {
  .main-sidebar {
    width: 300px;
  }
}
.main-content {
  order: 1;
  flex-grow: 1;
  padding: 20px 0;
}

.footer {
  display: flex;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 50px;
  border-top: 1px solid #ccc;
}
.footer-copyright {
  margin-left: 20px;
}

h1 {
  margin-bottom: 32px;
}

p {
  margin-bottom: 16px;
}

ul:not([class]) li {
  margin-top: 1em;
}

code {
  display: inline-block;
  vertical-align: baseline;
  padding: 0 0.25em;
  background-color: #f0f0f0;
  border-radius: 0.125em;
}

.tabs .tab-header-wrap {
  display: flex;
  padding: 0 1px;
}
.tabs .tab-header {
  padding: 8px 20px;
  font-weight: bold;
  transition: color 0.2s linear;
}
.tabs .tab-header.active {
  box-shadow: 0 1px 0 #fff, 0 0 0 1px #ccc;
}
.tabs .tab-header:not(.active) {
  cursor: pointer;
}
.tabs .tab-header:not(.active):hover {
  color: #e80;
}
.tabs .tab-content-wrap {
  border: 1px solid #ccc;
}
.tabs .tab-content {
  padding: 30px 20px;
}
.tabs .tab-content:not(.active) {
  display: none;
}
.tabs .tab-content:active {
  cursor: grab;
}
.tabs .tab-content::-webkit-scrollbar {
  /* спрятать полосу прокрутки в Chrome */
  display: none;
}
.tabs .tab1-content,
.tabs .tab2-content {
  padding: 0;
  user-select: none;
  overflow: hidden;
  max-width: 858px;
  max-height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1279px) {
  .tabs .tab1-content,
.tabs .tab2-content {
    max-width: 478px;
    max-height: 478px;
  }
}
@media (max-width: 767px) {
  .tabs .tab1-content,
.tabs .tab2-content {
    max-width: 298px;
    max-height: 298px;
  }
}